<template>
  <div class="cityList">
      <div class="circle_bg">
          <div class="circle_inner">
              <div class="search_fixed">
                  <div class="input_wrap">
                        <input type="text" placeholder="请输入城市名称" v-model="inputCity">
                        <!-- <button>
                            <svg t="1562739254748" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2048" width="32" height="32"><path d="M435.293184 100.206592c-183.803904 0-332.8 148.996096-332.8 332.8 0 183.820288 148.996096 332.8 332.8 332.8 183.820288 0 332.8-148.979712 332.8-332.8C768.093184 249.202688 619.113472 100.206592 435.293184 100.206592zM435.35872 703.699968c-149.500928 0.031744-270.708736-121.15968-270.72512-270.660608 0-0.016384 0-0.016384 0-0.032768 0.016384-149.48352 121.176064-270.644224 270.660608-270.659584l0-0.049152c149.499904 0 270.72512 121.176064 270.72512 270.676992C706.051072 582.474752 584.858624 703.6672 435.35872 703.699968z" p-id="2049" fill="rgb(56, 56, 56)"></path><path d="M677.158912 632.361984 921.493504 876.6976 874.596352 923.790336 630.228992 679.421952Z" p-id="2050" fill="rgb(56, 56, 56)"></path></svg>
                        </button> -->
                    </div>
                    <div class="result">
                        <ul v-if="results.length>0">
                            <li v-for="(result ,index) in results" v-if="!noData" @click="addSearch(result)">{{result}}</li>
                        </ul>
                        <div class="no_data" v-if="noData && inputCity" style="text-align:center;">-没有您要查找的城市-</div>
                    </div>
              </div>
              <!-- <div class="title">历史搜索</div>
              <ul class="all_city">
                  <li>
                    吉林
                  </li>
              </ul> -->
              <div v-for="(province ,index) in cities" style="margin-top:30px;">
                  <div class="title">{{province.province}}</div>
                  <ul class="all_city">
                      <li v-for="(city ,index) in province.city" @click="add(city.cityName)">
                          {{city.cityName}}
                      </li>
                  </ul>
              </div>
          </div>
      </div>
      <div class="cityList_btn" @click="goBack()">
          <p>«</p>
          <p>返回首页</p>
      </div>
  </div>
</template>

<script type="text/ecmascript-6">
import getCity from "@/common/js/city";

export default {
name: 'cityList',
  data() {
    return {
        cities:[],
        inputCity:'',
        results:[],
        noData : false,

    }
  },
  components: {

  },
  methods:{
    goBack(){
        this.$router.push({name:'index'})
    },
    add(cityName){
        let list = JSON.parse(localStorage.getItem('cityList'))
        let index = list.indexOf(cityName)
        if(index === -1){
            list.push(cityName)
        }else{
            list.splice(index,1)
            list.push(cityName)
        }
        localStorage.setItem('cityList', JSON.stringify(list))
        console.log('添加成功')

        this.goBack()
    },
    search(){
        if(!this.inputCity){
            return
        }
        this.cities.forEach((item)=>{
            item.city.forEach((i)=>{
                if(i.cityName.indexOf(this.inputCity) > -1 ){
                    this.results.push(`${item.province}， ${i.cityName}`)
                }
            })
        })
        if(this.results.length === 0){
            this.noData = true
        }else{
            this.noData = false
        }
    },
    addSearch(result){
        let index = result.indexOf('，')
        let cityName = result.substring(index+2)
        this.add(cityName)
    }
  },
  mounted(){
      this.cities = getCity()
      
  },
  watch:{
      inputCity(n){
          this. results = []
          this.search()
      }
  }
}
</script>

<style scoped lang="less">
.cityList{
    background: #0e71c3;
    overflow: hidden;
}
.circle_bg{
    background: linear-gradient(90deg,#b5acef, #7cb1e0);
    width:172vw;
    height: 100vh;
    border-bottom-right-radius: 50%;
    margin-left: -70%;
    box-shadow: 0 0 10px 10px rgba(0,0,0,0.1);
    overflow-y: auto;
    overflow-x: hidden;
}
.cityList_btn{
    position: absolute;
    bottom:20px;
    right:20px;
    color: #fff;
    text-align: center;

    &:active{
        opacity: 0.7;
    }

    p{
        &:nth-child(1){
            font-size: 5em;
        }
        &:nth-child(2){
            font-size: 0.7em;
            margin-top: -15px;
        }
    }
}
.circle_inner{
    float: right;
    width:101vw;
    overflow: hidden;
    margin-bottom: 200px;
}
.input_wrap{
    overflow: hidden;
    background: rgba(255, 255, 255, 0.9);
    input{
        float: left;
        width:74%;
        height: 50px;
        line-height: 75px;
        padding: 0 3%;
        background: transparent;
        color: rgb(56, 56, 56);
    }

    button{
        background: transparent;
        text-align: center;
        width:12%;

        &:active{
            opacity: 0.5;
        }

        svg{
            width:20px;
            height: 20px;
            margin: 10px 0 5px 0;
        }
    }
}
.title{
    color:rgb(238, 238, 238);
    padding:30px 10px 15px 10px;
}
.all_city{
    overflow: hidden;
    width: calc(100vw - 25px);
    margin-left: 10px;

    li{
        float: left;
        width:calc((1/4*100%) - 1px);
        text-align: center;
        line-height: 40px;
        height: 40px;
        background: rgba(255,255,255,0.2);
        margin-bottom: 1px;
        margin-right: 1px;
        color: rgb(255, 255, 255);
        font-size: 0.9em;

        &:active{
            background: rgba(255,255,255,0.08);
        }
    }
}
.result{
    .no_data,li{
        line-height: 40px;
        height: 40px;
        background: rgba(255,255,255,0.8);
        margin-top: 1px;
        padding: 0 10px;
        font-size: 0.9em;
        color: rgb(56, 56, 56);

        &:active{
            background: rgba(255,255,255,0.7);
        }
    }
}
.search_fixed{
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    z-index: 2;
}
// @media screen and (min-height: 560px){
//     .circle_bg{
//         width:172vw;
//     }
// }
</style>



