// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'

import VueScroller from 'vue-scroller'
import VueLazyload from 'vue-lazyload'

Vue.config.productionTip = false

Vue.use(VueScroller)

Vue.use(VueLazyload, {
    preLoad: 1.3,
    error: require('@/assets/logo2.png'),
    loading: require('@/assets/logo3.png'),
    attempt: 1
})

var u = navigator.userAgent;

if (u.indexOf('APICloud') > -1) {
    // 手机端
    // 将new Vue()放在 window.apiready 中，确保apiCloud的语法在组件中有效
    window.apiready = () => {
        new Vue({
            el: '#app',
            router,
            store,
            components: { App },
            template: '<App/>'
        })
    }
} else {
    new Vue({
        el: '#app',
        router,
        store,
        components: { App },
        template: '<App/>'
    })
}


