<template>
  <div id="app">
    <!-- <router-view/> -->
    <!-- <transition :name="transitionName"> -->
        <router-view/>
    <!-- </transition> -->
    <!-- <index></index> -->
    <!-- <tab></tab> -->
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import index from '@/view/index';
import tab from '@/view/tab';
import "./common/less/animate.min.css";

export default {
  name: 'App',
  data() {
    return {
      transitionName:''
    }
  },
  computed:{
    ...mapGetters([
    ])
  },
  components: {
    index,
    tab
  },
  methods:{
    ...mapMutations([
      'SET_ISSHOWPAGE',// 展示二级页
    ]),
    goBack(){
      this.$router.go(-1);
      this.SET_ISSHOWPAGE(false)
    }
  },
  mounted(){
    
  },
  watch: {
    // $route (to, from) {
    //   // 添加转场动画
    //   if (to.meta.index > from.meta.index) {
    //     this.transitionName = 'slide-left'
    //   } else {
    //     this.transitionName = 'slide-right'
    //   }
    // }
  }
}
</script>

<style scoped lang="less">
@import "./common/less/index.less";
#app{
  height: 100vh;
  background: #fff;
}

//转场动画
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active 
{
  // 启用硬件加速
  will-change: transform;
  transition: all 500ms;
  position: fixed;
  width: 100%;
}
.slide-right-enter {
  transform: translate(-100%, 0);
  transition-timing-function: linear;
}
.slide-left-enter {
  transform: translate(100%, 0);
  transition-timing-function: linear;
}
.slide-right-leave {
  transform: translate(100%, 0);
  transition-timing-function: linear;
}
.slide-left-leave {
  transform: translate(-100%, 0);
  transition-timing-function: linear;
}
</style>



