import * as types from './mutationTypes'
import state from './state'

const mutations = {
    [types.SET_ISSHOWPAGE](state, isShowPage) {
        state.isShowPage = isShowPage
    },
    [types.SET_HEADERTITLE](state, headerTitle) {
        state.headerTitle = headerTitle
    },
    [types.SET_SECONDPATH](state, secondPath) {
        state.secondPath = secondPath
    },
    [types.SET_ISNIGHT](state, isNight) {
        state.isNight = isNight
    }

}
export default mutations


