<template>
  <div class="cityList">
      <confirm :show="show" @childNext="childNext"></confirm>
      <div class="circle_bg">
          <ul>
              <li v-for="(cityBox , index) in cityBoxes">
                  <div @click="goToIndex(index)">
                      <div class="city_block_top">
                          <img :src="handleBg(cityBox.wea_img)" alt="">
                          <div class="city_block_top_word">
                              <p>{{cityBox.tem.substr(0,2)}}
                                  <span>℃</span> </p>
                              <p>{{cityBox.wea}}</p>
                          </div>
                      </div>
                      <div class="city_block_bottom">
                              <img :src="require(`@/assets/cityList_white_img.png`)" alt="">
                              <div class="city_block_bottom_word">
                                  <p>{{cityBox.city}}</p>
                                  <p>{{cityBox.cityEn | toUpperCase}}</p>
                              </div>
                      </div>
                  </div>
                  
                  <div class="detele_btn" @click="isDelete(index)" v-if="isEdit">删除</div>
              </li>
              <li @click="goToAddCity()" >
                  <div class="city_block_top" style="background:rgba(255,255,255,0.5);">
                      <!-- <img :src="handleBg(cityBox.wea_img)" alt=""> -->
                      <div class="city_block_top_word" >
                          <p style="margin:-20px 0 0 0;font-size:5em;">+</p>
                      </div>
                  </div>
                  <div class="city_block_bottom">
                        <img :src="require(`@/assets/cityList_white_img.png`)" alt="">
                        <div class="city_block_bottom_word">
                            <p>添加城市</p>
                            <p>TIANJIA</p>
                        </div>
                  </div>
              </li>
          </ul>
      </div>
      <div class="cityList_btn" @click="edit()">
          <p>{{editBtns.icon}}</p>
          <p>{{editBtns.txt}}</p>
      </div>
  </div>
</template>

<script type="text/ecmascript-6">
import confirm from '@/components/confirm'
export default {
name: 'cityList',
  data() {
    return {
        cityBoxes:[],
        Loop:null,
        isEdit:false,
        show:false,
        delIndex:null
    }
  },
  components:{
      confirm
  },
  computed: {
      editBtns(){
          let btn = {}
          if(this.isEdit){
              btn = {
                  icon:'×',
                  txt:'取消'
              }
          }else{
              btn = {
                  icon:'☍',
                  txt:'编辑'
              }
          }
          return btn
      }
  },
  methods:{
      // 动态设置每个城市天气背景图片
    handleBg(str){
        switch (str) {
            case 'qing':
                return require(`@/assets/weather_bg_q.jpg`)
                break;
            case 'yun':
                return require(`@/assets/weather_bg_dyun.jpg`)
                break;
            case 'yu':
                return require(`@/assets/weather_bg_xy.jpg`)
                break;
            case 'yin':
                return require(`@/assets/weather_bg_y.jpg`)
                break;
            case 'lei':
                return require(`@/assets/weather_bg_lzy.jpg`)
                break;
            default:
                return require(`@/assets/weather_bg_scb.jpg`)
                break;
        }
    },
    goToIndex(index){
        this.$router.push({name:'index',params:{index}})
    },
    goToAddCity(){
        this.$router.push({name:'addCity'})
    },
    isDelete(index){
        this.show = true
        this.delIndex = index
    },
    edit(){
        if(this.cityBoxes.length === 1){
            return
        }
        this.isEdit = !this.isEdit
    },
    childNext(isNext){
        if(isNext){
              this.deleteCity(this.delIndex)
        }
        this.show = false
    },
    deleteCity(index){
        this.cityBoxes.splice(index,1)
        let cityList = JSON.parse(localStorage.getItem('cityList'))
        cityList.splice(index,1)
        localStorage.setItem('cityList', JSON.stringify(cityList))

        if(this.cityBoxes.length ===1){
            this.isEdit = false
        }
    },
  },
  filters:{
      toUpperCase(val){
          return val.toUpperCase()
      }
  },
  mounted(){
      this.cityBoxes = this.$route.params.cityBoxes
  }
}
</script>

<style scoped lang="less">
.cityList{
    background: #0e71c3;
    overflow: hidden;
}
.circle_bg{
    background: linear-gradient(90deg,#b5acef, #7cb1e0);
    width:174vw;
    height: 100vh;
    border-bottom-right-radius: 50%;
    margin-left: -70%;
    box-shadow: 0 0 10px 10px rgba(0,0,0,0.1);
    overflow-x: hidden;
    overflow-y: auto;
    position: relative;
    z-index: 1;

    ul{
        float: right;
        width:100vw;
        overflow: hidden;
        margin-bottom: 200px;

        li{
            float: left;
            width:34%;
            margin: 40px 0 0 8%;
            box-shadow: 0 0 3px 3px rgba(0,0,0,0.1);

            &:active{
                opacity: 0.7;
            }

            img{
                width: 100%;
                vertical-align: bottom;
            }
        }
    }
}
.city_block_top{
    height: 140px;
    overflow: hidden;
    position: relative;
}
.city_block_bottom{
    text-align: center;

    img{
        display: block;
        margin-top: -13px;
        position: relative;
    }

    p{
        &:nth-child(2){
            opacity: 0.5;
            font-size: 0.6em;
            margin-top: 3px;
        }
    }
}
.city_block_bottom_word{
    background: #fff;
    padding:5px 0 15px 0;
}
.cityList_btn{
    position: absolute;
    bottom:20px;
    right:20px;
    color: #fff;
    text-align: center;

    &:active{
        opacity: 0.7;
    }

    p{
        &:nth-child(1){
            font-size: 5em;
        }
        &:nth-child(2){
            font-size: 0.7em;
            margin-top: -15px;
        }
    }
}
.city_block_top_word{
    position: absolute;
    top:35px;
    left: 0;
    color: #fff;
    text-align: center;
    width:100%;

    &:active{
        opacity: 0.7;
    }

    p{
        &:nth-child(1){
            font-size: 2em;
            margin-left: 18px;
            span{
                font-size: 0.5em;
                display: inline-block;
                vertical-align: top;
                margin-left: -5px;
            }
        }
        &:nth-child(2){
            font-size: 0.8em;
        }
    }
}
.detele_btn{
    text-align: center;
    background: rgb(212, 4, 4);
    color:#fff;
    padding: 8px 0;
    font-size: 0.7em;

    &:active{
        background: rgb(170, 0, 0);
    }
}
// @media screen and (min-height: 560px){
//     .circle_bg{
//         width:174vw;
//     }
// }
</style>



