import Vue from 'vue'
import Router from 'vue-router'
import index from '@/view/index'
import cityList from '@/view/cityList'
import addCity from '@/view/addCity'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'index',
        component: index,
        meta: { index: 1 }
    }, {
        path: '/cityList',
        name: 'cityList',
        component: cityList,
        meta: { index: 2 }
    }, {
        path: '/addCity',
        name: 'addCity',
        component: addCity,
        meta: { index: 3 }
    }]
})


