<template>
  <div class="index">
      <img :src="whiteBg" alt="" class="white_bg">
      <tab :itemLength="citiesWeather.length" ref="tab">
          <div class="tab_scroll_item" v-for="(cityWeather,index) in citiesWeather">
                <div class="index_bg">
                    <img :src="handleBg(cityWeather.weathers[0].wea_img)" alt="">
                </div>
                <!-- <div class="index_bg" :style="{backgroundImage:`url(${handleBg(cityWeather.weathers[0].wea_img)})`,height:'139vw'}"> -->
                <div class="index_body">
                    <div class="index_top">
                        <div class="index_top_left" @click="goToCityList()">
                            <span style="font-size:1.4em;margin-top:-5px;display:inline-block;">»</span>
                            {{cityWeather.city}} 
                        </div>
                        <div class="index_top_right">
                            <span>{{cityWeather.weathers[0].wea}}</span>
                            <weather-icon :weatherTxt="cityWeather.weathers[0].wea_img" :color="'#fff'" class="top_weather_icon"></weather-icon>
                        </div>
                    </div>
                    <div class="index_main" @click="more = cityWeather;showMore = true">
                        <div class="now_tem">
                            {{cityWeather.weathers[0].tem.substr(0,2)}} <span>℃</span>
                        </div>
                        <div class="other_tem">
                            <p>{{cityWeather.weathers[0].tem1}} 
                                <span> / {{cityWeather.weathers[0].tem2}}</span>
                            </p>
                            <p>空气质量：{{cityWeather.weathers[0].air_level}}</p>
                        </div>
                    </div>
                    <div class="index_bottom">
                        <ul>
                            <li v-for="(weather,index) in cityWeather.weathers" v-if='index>0 && index<6'>
                                <div style="font-size:0.8em;">
                                    <p>{{weather.week}}</p>
                                    <p>{{weather.date.substr(5,5)}}</p>
                                </div>
                                <div class="index_icon_wrap weather_icon">
                                     <!-- <svg t="1562305849475" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5555" width="30" height="30"><path d="M513.096 806.441c-162.438 0-294.586-131.659-294.586-293.513 0-161.835 132.149-293.495 294.586-293.495 162.435 0 294.578 131.659 294.578 293.495 0 161.854-132.143 293.513-294.578 293.513z m0-538.239c-135.443 0-245.646 109.785-245.646 244.729 0 134.96 110.2 244.764 245.646 244.764 135.442 0 245.629-109.804 245.629-244.764 0-134.944-110.188-244.729-245.629-244.729zM513.096 137.993c-13.514 0-24.466-10.92-24.466-24.391V28.875c0-13.471 10.952-24.375 24.466-24.375 13.521 0 24.475 10.904 24.475 24.375v84.728c0 13.47-10.956 24.39-24.475 24.39zM113.459 537.321H28.424c-13.513 0-24.466-10.903-24.466-24.374 0-13.473 10.954-24.375 24.466-24.375h85.036c13.521 0 24.475 10.901 24.475 24.375-0.001 13.471-10.955 24.374-24.476 24.374zM862.541 885.517c-6.27 0-12.537-2.365-17.307-7.135l-66.872-66.639c-9.567-9.536-9.567-24.977 0-34.479 9.556-9.535 25.061-9.535 34.611 0l66.854 66.64c9.569 9.521 9.569 24.958 0 34.479-4.764 4.766-11.034 7.134-17.286 7.134zM230.505 255.798a24.443 24.443 0 0 1-17.306-7.152l-66.89-66.638c-9.553-9.52-9.553-24.958 0-34.478 9.563-9.521 25.05-9.521 34.604 0l66.888 66.638c9.562 9.52 9.562 24.958 0 34.478-4.775 4.768-11.036 7.152-17.296 7.152zM163.6 885.534c-6.26 0-12.529-2.385-17.306-7.152-9.553-9.504-9.553-24.958 0-34.479l66.888-66.64c9.563-9.52 25.05-9.52 34.611 0 9.553 9.521 9.553 24.958 0 34.479l-66.896 66.639a24.406 24.406 0 0 1-17.297 7.153zM795.637 255.798c-6.271 0-12.537-2.384-17.308-7.136-9.567-9.537-9.567-24.958 0-34.478l66.905-66.655c9.555-9.52 25.059-9.52 34.595 0 9.569 9.537 9.569 24.975 0 34.478l-66.889 66.655a24.499 24.499 0 0 1-17.303 7.136z" :fill="color" p-id="5556"></path><path d="M795.67 255.798a24.414 24.414 0 0 1-17.289-7.152c-9.555-9.52-9.555-24.958 0-34.478l66.889-66.638c9.553-9.521 25.041-9.521 34.595 0 9.569 9.52 9.569 24.958 0 34.478l-66.889 66.638