                                    <weather-icon :weatherTxt="weather.wea_img"></weather-icon>
                                    <!-- <weather-icon></weather-icon> -->
                                </div>
                                <div>
                                    <p style="transform:scale(0.7,0.7);">{{weather.tem1}}/{{weather.tem2}}</p>
                                    <p style="mrgin_top:-3px;transform:scale(0.8,0.8);">{{weather.wea}}</p>
                                </div>
                            </li>
                            
                        </ul>
                    </div>
                </div>
          </div>
      </tab>
      <!-- 详细信息 -->
      <div class="more" v-if="showMore" @click="showMore=false">
          <div class="more_cityName">{{more.city}} <span>{{more.weathers[0].tem}}</span></div>
          <div class="more_time">更新时间：{{more.updateTime.substr(11,5)}}</div>
          <ul>
              <li v-if="more.weathers[0].alarm.alarm_content">
                  <span>预警</span><div>{{more.weathers[0].alarm.alarm_content}}</div>
              </li>
              <li>
                  <span>湿度：</span><div>{{more.weathers[0].humidity}}</div>
              </li>
              <li>
                  <span>白天/夜晚：</span><div>{{more.weathers[0].tem1}} / {{more.weathers[0].tem2}}</div>
              </li>
              <li>
                  <span>风向：</span><div>{{more.weathers[0].win[0]}}</div>
              </li>
              <li>
                  <span>风速：</span><div>{{more.weathers[0].win_speed}}</div>
              </li>
              <li>
                  <span>空气质量：</span><div>{{more.weathers[0].air_level}}</div>
              </li>
              <li>
                  <span>建议：</span><div>{{more.weathers[0].air_tips}}</div>
              </li>
          </ul>

          <ul>
              <li v-for="(one ,index) in more.weathers[0].index" v-if="index!==1">
                  <span>{{one.title}}：</span><div><i>{{one.level}}</i> {{one.desc}}</div>
              </li>
               <li v-for="(one ,index) in more.weathers[0].index" v-if="index===1">
                  <span>减肥指数：</span><div>{{one.desc}}</div>
              </li>
          </ul>
      </div>
      <!-- <weather-icon :weatherTxt="'雾霾'"></weather-icon> -->
  </div>
</template>

<script type="text/ecmascript-6">
import { mapGetters, mapMutations } from "vuex";
import axios from "axios";
import weatherIcon from "@/components/weatherIcon";

import getCity from "@/common/js/city";
import tab from "@/view/tab";

export default {
  name: 'index',
  data() {
    return {
        today:{},
        whiteBg:require('@/assets/white_bg.png'),
        citiesWeather:[],
        cityList:[],
        color:'#000',
        sortNum:-1,
        cityBoxes:[],
        showMore:false,
        more:{}
    }
  },
  components: {
      weatherIcon,
      tab
  },
  computed:{
    ...mapGetters([
    ])
  },
  methods:{
    ...mapMutations([
    ]),
    goToCityList(){
        this.citiesWeather.forEach((item)=>{
            
            this.cityBoxes.push({
                city : item.city,
                tem : item.weathers[0].tem,
                wea : item.weathers[0].wea,
                wea_img : item.weathers[0].wea_img,
                cityEn : item.cityEn
            })
        })
        this.$router.push({name:'cityList',params:{cityBoxes : this.cityBoxes}})
        
    },
    // 动态设置每个城市天气背景图片
    handleBg(str){
        // let key = parseInt(index)
        switch (str) {
            case 'qing':
                return require(`@/assets/weather_bg_q.jpg`)
                break;
            case 'yun':
                return require(`@/assets/weather_bg_dyun.jpg`)
                break;
            case 'yu':
                return require(`@/assets/weather_bg_xy.jpg`)
                break;
            case 'yin':
                return require(`@/assets/weather_bg_y.jpg`)
                break;
            case 'lei':
                return require(`@/assets/weather_bg_lzy.jpg`)
                break;
            default:
                return require(`@/assets/weather_bg_scb.jpg`)
                break;
        }
    },
    // 获取天气数据
    getData(cityName,code = 101010100){
        //  console.log('开始：'+this.citiesWeather.length,cityName)
        let url = `https://www.tianqiapi.com/api/?version=v1&cityid=${code}`
        axios.get(url)
        .then((res)=> {
            let all = res.data
            // console.log(res)
            // 按照城市数组原始顺序存放天气数据
            this.citiesWeather.push({
                city : cityName,
                weathers : all.data,
                cityEn : all.cityEn,
                updateTime: all.update_time
            })

            // 全部天气数组获取到后，进行排序。
            // 因为是异步获取，得到数据时间不同，顺序是乱的，所以需要排序
            if(this.citiesWeather.length === this.cityList.length){
                this.citiesWeather.sort((a,b)=>{
                    let index1 = this.cityList.indexOf(a.city) 
                    let index2 = this.cityList.indexOf(b.city) 
                    return index1 - index2
                })
                // console.log(this.cityBoxes)
            }
        })
        .catch((err)=> {
            console.log(err);
        });
    },
    // 获取城市编码
    getCode(cityName){
        let name = cityName || '吉林市'
        let cityTxt = JSON.stringify(getCity())
        let index = cityTxt.indexOf(name)
        let length = index + name.length + 14
        let code = cityTxt.substr(length,9)
        // 处理特殊情况，自治区省市同名
        if(code === 'tyName":"'){
            code = cityTxt.substr(length+25,9)
        }
        // console.log(cityName+'编码：'+code)

        this.getData(cityName,code)
    },
    // 获取本地存储的城市列表
    getCityList(){
        if(localStorage.getItem('cityList')){
            this.cityList = JSON.parse(localStorage.getItem('cityList'))
            this.getCode(this.cityList[0])
            this.cityList.forEach((element,index) => {
                if(index !== 0){
                    this.getCode(element)
                }
            })
        }else{
            this.cityList = []
        }
    },
  },
  mounted(){

      
    //   let c = ['吉林市','亳州','郑州','青岛']
    //   localStorage.setItem('cityList', JSON.stringify(c))
    //   console.log(localStorage.getItem('cityList'))

      if(!localStorage.getItem('cityList')){
          let c = ['吉林市']
          localStorage.setItem('cityList', JSON.stringify(c))
      }

      this.getCityList()

       if(this.$route.params.index){
           setTimeout(() => {
               this.$refs.tab.tabClick(this.$route.params.index)
           }, 500);
       }
  }
}
</script>

<style scoped lang="less">
@import "../common/less/index.less";
.index{
    background: #fff;
}
.index_body{
    position: relative;
    z-index: 2;
    // background-size: cover;
}
.tab_scroll_item{
    position: relative;
}
.index_bg{
    position: absolute;
    top:0;
    left: 0;
    width: 100vw;
    height: 70vh;
    overflow: hidden;
    z-index: 0;

    img{
        display: block;
        width:100%;
    }
}
.weather_img{
    width:100%;
}
.white_bg{
    position: fixed;
    width: 100vw;
    top: 58vh;
    left: 0;
    z-index:2;
}

.index_top{
    overflow: hidden;
    padding-top: 15px;
    margin: 0 15px;
    color: #fff;
}
.index_top_left{
    float: left;

    &:active{
        opacity: 0.6;
    }
}
.index_top_right{
    float: right;
}
.index_main{
    color:#fff;
    text-align: center;
    margin-top: 20%;
}
.now_tem{
    font-size: 5em;
    position: relative;

    span{
        font-size: 0.5em;
        position: absolute;
        right:27%;
        top: 10px;
    }
}
.other_tem{
    p{
        margin-bottom: 10px;
        span{
            opacity: 0.7;
        }

        &:nth-child(2){
            font-size: 0.85em;
        }
    }
}
.index_bottom{
    text-align: center;
    margin-top: 31vh;
    position: relative;
    z-index: 4;

    ul{
        overflow: hidden;

        li{
            float: left;
            width:1/5*100%;
            font-size: 0.9em;
        }
    }
}
.index_icon_wrap{
    margin: 10px 0 5px 0;
}
.top_weather_icon{
    display: inline-block;
    vertical-align: middle;
    margin-left: 5px;
}
.more{
    position: fixed;
    top:0;
    left: 0;
    z-index: 3;
    width:100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    background: rgba(255,255,255,0.95);

    ul{
        width:90%;
        margin: 0 auto;
        font-size: 0.85em;

        li{
            overflow: hidden;
            margin-bottom: 15px;
        }

        span{
            opacity: 0.4;
            display: inline-block;
            width:125px;
            line-height: 22px;
        }

        div{
            float: right;
            width:calc(90vw - 135px);
            line-height: 22px;

            i{
                color: rgb(153, 153, 153);
                font-size: 0.8em;
                display: inline-block;
                margin-right: 8px;
            }
        }
    }
}
.more_cityName{
    font-size:1.6em;
    font-weight: bold;
    text-align: center;
    margin:40px 0 10px 0;
    color: rgb(233, 31, 31);

    span{
        font-size: 14px;
    }
}
.more_time{
    font-size: 0.7em;
    opacity: 0.7;
    margin-bottom: 20px;
    text-align: center;
}
@media screen and (min-height: 550px){
    .index_bottom{
        margin-top: 26vh;
    }
    .index_bg{
        height: 70vh;
    }
    .white_bg{
        top: 58vh;
    }
}
@media screen and (min-height: 600px){
    .index_bottom{
        margin-top: 32vh;
    }
}
@media screen and (min-height: 690px){
    .index_bottom{
        margin-top: 36vh;
    }
}
@media screen and (min-height: 750px){
    .index_bottom{
        margin-top: 39vh;
    }
}
</style>



