<template>
  <div class="tab">
      <ul class="tab_btn">
          <li v-for="(tab , index) in tabs" class="nagtive" :class="{'active':curIndex === index}" @click="tabClick(index)">{{tab}}</li>
      </ul>
      <div class="tab_scroll_wrapper">
          <div class="tab_scroll_content" ref="ts"  @touchstart="touchStart" @touchend="touchEnd" @touchmove="touchMove" :style="{transform:`translate3d(${mgL}px,0,0)`}">
              <div ref="ts">
                  <slot></slot>
                  <!-- <div class="tab_scroll_item tab_body_1"><div>1</div></div>
                  <div class="tab_scroll_item tab_body_2"><div>2</div></div> -->
              </div>
          </div>
      </div>
  </div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll'

export default {
name: 'tab',
  data() {
    return {
        // tabs:['',''],
        curIndex:0,
        screenWidth:0,
        startX:0,
        endX:0,
        moveX:0,
        isRecord:true,
        mgL:0,
        moveCutW:0,
        startMgl:0,
        limitW:70,
        startPos: {},
        widthStyle:0
    }
  },
  components: {

  },
  props:{
      itemLength:{
          type:Number,
          default(){
              return 2
          }
      }
  },
  computed:{
      tabs(){
          let result = []
          let num = this.itemLength
          for(let i = 0; i<num;i++){
              result.push('')
          }
          return result
      },
      //   --------------------------------------------------------------------------active时，line 样式改变
      lineStyle(){
          
          // 小于一定偏移量时，不滑动 
          if(Math.abs(this.moveCutW) < this.limitW){
              return this.curIndex
          }
          // 开头结尾，边界判断
          if((this.curIndex === this.tabs.length-1) && (this.moveCutW < 0)){
              return
          }
          if((this.curIndex === 0) && (this.moveCutW > 0)){
              return
          }
          let num = this.curIndex - this.moveCutW / this.screenWidth
          return num
      }
  },
  methods: {
      touchStart(e){
          this.startX = parseInt(e.changedTouches[0].clientX)
          this.startMgl = this.mgL
           //   阻止横向默认行为 ，定义初始量
          let touch = e.targetTouches[0]
          this.startPos = {x:touch.pageX,y:touch.pageY}
      },
      touchMove(e){
          //   阻止横向默认行为
          let touch = e.targetTouches[0]
          let endPos = {x:touch.pageX - this.startPos.x,y:touch.pageY - this.startPos.y}
        　let isScrolling = Math.abs(endPos.x) < Math.abs(endPos.y) ? 1:0 //isScrolling为1时，表示纵向滑动，0为横向滑动
        　if(isScrolling === 0){
            e.preventDefault() //阻止触摸事件的默认行为，即阻止滚屏
        　}

          //   真正开始，当前横坐标
          this.moveX = e.changedTouches[0].clientX
          //   手指移动偏移量
          this.moveCutW = this.moveX - this.startX
          // 小于一定偏移量时，不滑动 
          if(Math.abs(this.moveCutW) < this.limitW){
              return
          }
          // 开头结尾，边界判断
          if((this.curIndex === this.tabs.length-1) && (this.moveCutW < 0)){
              return
          }
          if((this.curIndex === 0) && (this.moveCutW > 0)){
              return
          }
          this.mgL =  (this.startMgl + this.moveCutW)
          //   --------------------------------------------------------------------active时，line 样式改变
          this.widthStyle = Math.abs(this.moveCutW)  / this.screenWidth
      },
      touchEnd(e){
          
          if(Math.abs(this.moveCutW) < this.limitW){
              return
          }
          let w = this.screenWidth
          this.endX = parseInt(e.changedTouches[0].clientX)
          
          let cutW = this.endX - this.startX

          if(cutW < 0){
            //   左滑
            if(this.curIndex === this.tabs.length-1){
                return
            }
            this.mgL = -(this.curIndex+1)*w
            this.curIndex++

            this.$emit('changeCity',this.curIndex)
          }else{
              //   右滑
              if(this.curIndex === 0){
                  return
              }
            this.mgL = -(this.curIndex-1)*w
            this.curIndex--

            this.$emit('changeCity',this.curIndex)
          }
          //   重置，否则下次只点击时也滑动
          this.moveCutW = 0
          //   -----------------------------------------------------------active时，line 样式改变
          this.widthStyle = 0
      },
      // 点击按钮切换
      tabClick(index){
          let w = this.screenWidth
          this.curIndex = index
          let l =  w*index
          this.mgL = -this.curIndex*w
      }
  },
  mounted(){
      //   获取屏幕宽度
      this.screenWidth = document.body.clientWidth
  }
}
</script>

<style scoped lang="less">
@import "../common/less/index.less";
@tab_btn_height:30px;
.tab_btn{
    position: fixed;
    z-index: 1;
    bottom:10px;
    text-align: center;
    width:100%;

    li{
        display: inline-block;
        background: rgba(0,0,0,0.2);
        width:10px;
        height: 10px;
        border-radius: 50%;
        margin: 0 8px;
        transition: all 0.5s;

        &.active{
            width:20px;
            border-radius: 20px;
            color: #000!important;
        }
    }
}
// better-scroll必需------------------------开始
.tab_scroll_wrapper{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height:100vh;
    white-space: nowrap;
    overflow-y: hidden;
    overflow-x: scroll;
}
.tab_scroll_content{
    display: inline-block;
    white-space: nowrap;
    height: 100vh;
    letter-spacing: -.3333em;
    transition:all 0.5s;
}
.tab_scroll_item{
    width: 100vw;
    height: 100vh;
    display: inline-block;
    letter-spacing: 0;
    overflow-y: scroll;

}
</style>



