<template>
  <div class="confirm" v-if="show">
      <div class="confirm_wrap">
          <div class="confirm_txt">{{text}}</div>
          <div class="confirm_btns">
              <span @click="next(true)">确定</span>
              <span @click="next(false)">取消</span>
          </div>
      </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
name: 'confirm',
  data() {
    return {
    }
  },
  props:{
      text:{
          type:String,
          default(){
              return '是否删除？'
          }
      },
      show:{
          type:Boolean,
          default(){
              return false
          }
      }
  },
  methods: {
      next(isNext){
          this.$emit('childNext',isNext)
      }
  }
}
</script>

<style scoped lang="less">
.confirm{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
}
@bg:rgba(255,255,255,1);
.confirm_wrap{
    width:70%;
    margin: 70% auto;
    border-radius: 6px;
    overflow: hidden;
    box-shadow: 0 0 3px 3px rgba(1, 1, 1, 0.1);
    
}
.confirm_txt{
    text-align: center;
    background: @bg;
    text-align: center;
    padding: 25px 0;
    border-bottom: 1px solid #f7f7f7;
    color: red;
}
.confirm_btns{
    overflow: hidden;
    span{
        float: left;
        display: block;
        width:calc((100% - 1px)/2);
        margin: 0;
        background: @bg;
        text-align: center;
        padding: 8px 0;
        font-size: 0.9em;

        &:nth-child(1){
            border-right: 1px solid #f7f7f7;
            // color: red;
        }
        &:active{
            background: rgba(255,255,255,0.7);
        }
    }
}
</style>



